/* Footer.css */
footer {
    background-color: #ecd7d7;
    color: #fff;
    text-align: center;
    padding: 10px;
  }
  
  ul {
    list-style: none;
    padding: 0;
  }
  
  ul li {
    display: inline;
    margin-right: 10px;
  }
  
  ul li a {
    color: #fff;
    text-decoration: none;
  }
  