body {
  font-family: Arial, sans-serif;
  background-color: #f0f0f0;
  margin: 0;
  padding: 0;
}

.container {
  max-width: 800px;
  margin: 20px auto;
  background-color: #fff;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  transition: background-color 0.3s, color 0.3s;
}

h2 {
  text-align: center;
  color: #333;
}

h3 {
  margin-top: 20px;
  color: #555;
}

label {
  font-weight: bold;
}

p {
  margin-bottom: 10px;
}

input[type="text"],
input[type="date"],
select,
textarea {
  width: calc(100% - 10px);
  padding: 8px;
  margin: 5px 0;
  border: 1px solid #ccc;
  border-radius: 4px;
}

textarea {
  resize: vertical;
}

button {
  padding: 8px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  background-color: #007bff;
  color: #fff;
}

button:hover {
  background-color: #0056b3;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  position: relative;
  padding: 10px;
  background-color: #f9f9f9;
  margin-bottom: 10px;
  border-radius: 4px;
  box-shadow: 0 2px 2px rgba(0, 0, 0, 0.1);
  transition: background-color 0.3s, color 0.3s, box-shadow 0.3s;
}

li label {
  font-size: 18px;
  font-weight: bold;
}

span {
  margin-left: 10px;
  color: #888;
}

.edit-mode input[type="text"] {
  width: calc(100% - 20px);
  padding: 5px;
  margin-bottom: 5px;
}

.edit-mode textarea {
  width: calc(100% - 20px);
  padding: 5px;
  margin-bottom: 5px;
}

.delete-btn {
  position: absolute;
  top: 5px;
  right: 90px; /* Adjusted to provide space between Delete and Share buttons */
  background-color: #dc3545;
}

.share-btn {
  position: absolute;
  top: 5px;
  right: 5px;
  background-color: #28a745;
}

.completed-tasks {
  background-color: #f0f0f0;
  border: 1px solid #ccc;
  padding: 10px;
}

.completed-tasks li {
  background-color: #d4edda;
  border-color: #c3e6cb;
}

.completed-tasks li label {
  text-decoration: line-through;
  color: #155724;
}

/* Night Mode Styles */
body.night-mode {
  background-color: #333;
  color: #f0f0f0;
}

.container.night-mode {
  background-color: #222;
  color: #f0f0f0;
  transition: background-color 0.3s, color 0.3s;
}

h2.night-mode, h3.night-mode {
  color: #f0f0f0;
}

label.night-mode {
  color: #f0f0f0;
}

input[type="text"].night-mode,
input[type="date"].night-mode,
select.night-mode,
textarea.night-mode {
  border: 1px solid #888;
  background-color: #444;
  color: #f0f0f0;
}

button.night-mode {
  background-color: #0056b3;
}

button.night-mode:hover {
  background-color: #007bff;
}

li.night-mode {
  background-color: #444;
  color: #f0f0f0;
  box-shadow: 0 2px 2px rgba(255, 255, 255, 0.1);
  transition: background-color 0.3s, color 0.3s, box-shadow 0.3s;
}

.completed-tasks li.night-mode {
  background-color: #222;
  color: #f0f0f0;
  border-color: #333;
}